import React, { Component } from 'react';
import img1 from '../img/profilepic.jfif';
import "./css/profile.css"
import { PersonFill, Coin } from 'react-bootstrap-icons'

class Profile extends React.Component {
    render() { 
        return <React.Fragment>
            <button className="btn-toggle" data-bs-toggle="collapse" data-bs-target="#profile_cont"><PersonFill/></button>
            <div id="profile_cont" className='container-fluid bg-dark rounded collapse p-2'>
                <div className="row">
                    <div className=" col-12 d-grid justify-content-center img-cont m-auto">
                        <img src={img1} alt="" className="img-fluid m-auto" />
                    </div>
                    <div className="col-12 text-light p-4 desc-cont m-auto">
                        <div className="row m-auto">
                            <div className="col-12">
                                <div className="fs-6 col-8 fw-bold text-uppercase profile-label text-warning">Name</div>
                                <div className="fs-5 col-12 profile-data m-auto px-2">Nurul Hafiz Likhon</div>
                            </div>
                            <div className="col-12 row m-auto text-left p-0">
                                <div className="fs-6 col-8 fw-bold text-uppercase profile-label text-warning">Nickname</div><div className="fs-6 col-4 fw-bold text-uppercase profile-label text-warning">Token's</div>
                                <div className="fs-5 col-12 profile-data">
                                    <div className="row">
                                        <div className="col-8 px-4">Devilninja</div><div className="col-4 text-success px-4 d-flex"><div className="m-auto px-1">20</div><Coin className='text-warning m-auto col-6'/></div>
                                    </div>
                                </div>
                                <div className="fs-6 col-12 fw-bold text-uppercase profile-label text-warning">Email</div>
                                <div className="fs-5 col-12 profile-data px-4">de*****@gmail.com</div>
                                <div className="row m-auto mt-4">
                                    <button className="btn btn-outline-warning text-uppercase col-12 m-auto">Update</button><button className="btn btn-outline-light text-uppercase col-12 m-auto mt-2">Change Password</button>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
            </React.Fragment>;
    }
}
 
export default Profile;